import React, {Component} from 'react';
import Bodycontent from './bodycontent';
import Bodycontenttv from './bodycontenttv';
import Chat from './../chat/chat';

export default class Bodyhead extends Component {
    constructor(props) {
        super(props);
        this.toogglesidebar= this.toogglesidebar.bind(this);
        this.state = {
            active: false,
            pagetv: false,
            pagechat: false,
            pagehome: true,
        };
    }

    componentDidMount(){
        if(localStorage.getItem('authlogin')==='false'){
        window.location.href = "#/login";
        }
    }

    toogglesidebar(){
        const currentState = this.state.active;
        //alert(currentState);
        this.setState({ active: !currentState });
    };

    pagetv(){
        this.setState({ pagehome: false });
        this.setState({ pagechat: false });
        this.setState({ pagetv: true });
        alert(this.state.pagetv);
    };

    pagechat(){
        this.setState({ pagehome: false });
        this.setState({ pagechat: true });
        this.setState({ pagetv: false });
        alert(this.state.pagechat);
    };

    homepage(){
        this.setState({ pagehome: true });
        this.setState({ pagechat: false });
        this.setState({ pagetv: false });
        alert(this.state.pagehome);
    };

    render(){
        if(localStorage.getItem('authlogin')==='true'){
        return (
            <body className="main-body">
        <div className="wrapper">
                <nav id="sidebar" className={this.state.active ? 'active': null} >
                    <div className="sidebar-header">
                        <h3>Bootstrap Sidebar</h3>
                        <strong>BS</strong>
                    </div>
            
                    <ul className="list-unstyled components">
                        <li className="active">
                            <a href="#/dashboard" onClick={() =>this.homepage()}>
                                <i className="fa fa-home"></i>
                                Home
                            </a>
                        </li>
                        <li className="active">
                            <a href="#/dashboard" onClick={() =>this.pagetv()}>
                                <i className="fa fa-home"></i>
                                TV Page
                            </a>
                        </li>
                        <li className="active">
                            <a href="#/dashboard" onClick={() =>this.pagechat()}>
                                <i className="fa fa-home"></i>
                                Chat Page
                            </a>
                        </li>
                        <li>
                            <a href="">
                                <i className="fa fa-briefcase"></i>
                                About
                            </a>
                            <a href="#pageSubmenu" data-toggle="collapse" aria-expanded="false" className="dropdown-toggle">
                                <i className="fa fa-copy"></i>
                                Pages
                            </a>
                            <ul className="collapse list-unstyled" id="pageSubmenu">
                                <li>
                                    <a href="#">Page 1</a>
                                </li>
                                <li>
                                    <a href="#">Page 2</a>
                                </li>
                                <li>
                                    <a href="#">Page 3</a>
                                </li>
                            </ul>
                        </li>
                        <li>
                            <a href="">
                                <i className="fa fa-history"></i>
                                Logs
                            </a>
                        </li>
                        <li>
                            <a href="">
                                <i className="fa fa-user"></i>
                                Users
                            </a>
                        </li>
                    </ul>
            
                </nav>
            
                <div id="content">
            
                        <nav className="navbar navbar-expand-lg navbar-light bg-light">
                                <div className="container-fluid">
                
                                    <button type="button" id="sidebarCollapse" className="btn btn-info" onClick={() =>this.toogglesidebar()}>
                                        <i className="fa fa-align-left"></i>
                                        <span>Toggle Sidebar</span>
                                    </button>
                                    <button className="btn btn-dark d-inline-block d-lg-none ml-auto" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                        <i className="fa fa-align-justify"></i>
                                    </button>
                
                                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                                        <ul className="nav navbar-nav ml-auto">
                                            <li className="nav-item active">
                                                <a className="nav-link" href="#">Page</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#">Page</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#">Page</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#">Page</a>
                                            </li>
                                        </ul>
                                        <ul className="nav navbar-nav ml-auto">
                                        <div className="dropdown">
                                        <button type="button" className="btn btn-default dropdown-toggle" data-toggle="dropdown">
                                        <i className="fa fa-user"></i>
                                        </button>
                                        <div className="dropdown-menu dropdown-menu-right text-right">
                                            <a className="dropdown-item" href="#">Link 1</a>
                                            <a className="dropdown-item" href="#">Link 2</a>
                                            <a className="dropdown-item" href="#/logout">Logout</a>
                                        </div>
                                        </div>
                                        </ul>
                                    </div>
                                </div>
                            </nav>

                            <div className="wrapper">
                                <div className="container">
                                    {this.state.pagehome===true && (<Bodycontent />)}
                                    {this.state.pagetv===true && (<Bodycontenttv />)}
                                    {this.state.pagechat===true && (<Chat />)}
                                    <div className="footerspace"></div>
                                        <hr className="col-12" />
                                        <h4 className="text-center text-primary">Copyright &copy; 2019</h4>
                                    <div id="footer" className="fixed-bottom">

                                    </div>
                                </div>
                            </div>
                </div>
            </div>
            </body>
            );}
            if(localStorage.getItem('authlogin')==='false'){
                return (<div></div> );
            }
    }
}