import React from 'react';

import CssBaseline from '@material-ui/core/CssBaseline';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import LockIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import { isMobile } from "react-device-detect";
import withStyles from '@material-ui/core/styles/withStyles';

import firebase from './../firebase.js';
//import $ from 'jquery';

const styles = theme => ({
    root: {
      width: '100%',
    },
    layout: {
        width: 'auto',
        display: 'block', // Fix IE 11 issue.
        marginLeft: theme.spacing(3),
        marginRight: theme.spacing(3),
        [theme.breakpoints.up(400 + theme.spacing(3) * 2)]: {
          width: 400,
          marginLeft: 'auto',
          marginRight: 'auto',
        },
      },
      paper: {
        marginTop: theme.spacing(3),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        padding: `${theme.spacing(2)}px ${theme.spacing(3)}px ${theme.spacing(3)}px`,
      },
      avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
      },
      form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
      },
      submit: {
        marginTop: theme.spacing(3),
      },
      centeralign: {
        flexGrow: 1,
        textAlign: 'center',
      },
      contentbody: {
        height: '85vh',
      }
});

class Login extends React.Component {
    constructor(props) {
        super(props);

        this.email = React.createRef();
        this.password = React.createRef();
    }

    state = {
        errorlogin: '',
    }

    componentDidMount() {
      //$("div.container").css({display:'none'});
    }

    login = (e) => {
        e.preventDefault();

        const auth = firebase.auth();
        try{
          auth.signInWithEmailAndPassword(this.email.current.value,this.password.current.value)
            .then(() => { this.userauth_login(this.email.current.value);window.location.reload(false); })
            .catch(error => {   
              //alert(error.code)
              this.setState({ errorlogin: 'Invalid user or password' });
              setTimeout(function(){ 
                this.setState({ errorlogin: '',email: '',password: '' });
              }.bind(this), 2000);
              switch(error.code) {
                case 'auth/user-not-found':
                      alert('Incorect User or Password !');
                      break;
                case 'auth/wrong-password':
                  alert('Incorect User or Password !');
                  break;
                default:
                  break;
            }
          });
        }catch(err){
            alert("Error : ", err);
        }
        
    }

    userauth_login(id){
            localStorage.setItem('authlogin', 'true');
            localStorage.setItem('user_id', id);
            //window.location.reload();
            //return <Redirect to="/reactmaterial/"/>;
            //alert('logout!'+ localStorage.getItem('authlogin'));
            window.location.href = "#/dashboard";
    }

    render() {
        const { classes } = this.props;

        return (
        <div>
          <h2>Login</h2>
          <hr/>
          <React.Fragment>
        <CssBaseline />
        <main className={classes.layout}>
          <Paper className={classes.paper}>
            <Avatar className={classes.avatar}>
              <LockIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              Sign in
            </Typography>
            <div className={isMobile?classes.errorloginm:classes.errorlogin}>{this.state.errorlogin}</div>
            <form className={classes.form} method="post" onSubmit={this.login.bind(this)}>
              <FormControl margin="normal" required fullWidth>
                <InputLabel htmlFor="email">Email Address</InputLabel>
                <Input
                  type="email"
                  id="email" 
                  name="email"
                  inputRef={this.email}
                />
              </FormControl>
              <FormControl margin="normal" required fullWidth>
                <InputLabel htmlFor="password">Password</InputLabel>
                <Input
                  name="password"
                  type="password"
                  id="password"
                  inputRef={this.password}
                />
              </FormControl>
              <FormControlLabel
                control={<Checkbox value="remember" color="primary" />}
                label="Remember me"
              />
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={classes.submit}
              >
                Sign in
              </Button>
            </form>
          </Paper>
        </main>
      </React.Fragment>
        </div>
      );
    }
}

export default withStyles(styles)(Login);
    