import React, { Component } from "react";
import { TextField, List, ListItem, ListItemText } from "@material-ui/core";
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/core/Icon';
import classNames from 'classnames';
import firebase from "./../firebase";
import moment from 'moment'; 
import $ from 'jquery';

class Chatcomp extends Component {
    constructor(props) {
      super(props)
      this.state = { text: "", messages: [], btnsubmit: true }
    }
    componentDidMount() {
      this.getMessages()
    }
  
    onSubmit = event => {
      if (event.charCode === 13 && this.state.text.trim() !== "") {
        this.writeMessageToDB(this.state.text)
        this.setState({ text: "" })
      }
    }

    onSubmitbtn = () => {
      alert(this.state.text);
      alert('S-a facut!!');
      //this.setState({ btnsubmit: true });
      alert(this.state.btnsubmit);
      if( this.state.btnsubmit ){
        this.writeMessageToDB(this.state.text);
        this.setState({ text: "" });
        //this.refs.divmessage.scrollTop = this.refs.divmessage.scrollHeight;
        //alert(this.refs.divmessage.scrollTop);
        //alert(this.refs.divmessage.scrollHeight);
        this.setState({ btnsubmit: false });
        setTimeout(function(){
        $('#divmsg').scrollTop($('#divmsg')[0].scrollHeight);
        }, 2000);
      }
    }
  
    writeMessageToDB = message => {
      var curtime = moment().format("DD/MM/YYYY HH:mm:ss");
      //alert(curtime);
      firebase
        .database()
        .ref("/messages")
        .push({
          text: message,
          time: curtime,
          userid: localStorage.getItem('user_id')
        })
    }
  
    getMessages = () => {
      //alert(this.state.messages)
      var messagesDB = firebase
        .database()
        .ref("/messages")
        .limitToLast(500)
      messagesDB.on("value", snapshot => {
        let newMessages = []
        snapshot.forEach(child => {
          var message = child.val()
          newMessages.push({ id: child.key, text: message.text,time: message.time,userid: message.userid })
        })
        this.setState({ messages: newMessages })
        //alert(this.state.messages)
        $('#divmsg').scrollTop($('#divmsg')[0].scrollHeight);
        this.bottomSpan.scrollIntoView({ behavior: "smooth" })
      })
    }
  
    renderMessages = () => {
      return this.state.messages.map(message => (
        <ListItem>
          <ListItemText
          primary={message.userid}
          />
          <ListItemText
            className="ListItemText message"
            style={{ wordBreak: "break-word"}}
            secondary={message.time}
            primary={message.text}
          />
        </ListItem>
      ))
    }
  
    render() {
      const divStyle={
        overflowY: 'scroll',
        border:'1px solid red',
        float: 'left',
        height:'500px',
        position:'relative'
      };
      return (
        <div className="Chat">
          <div id="divmsg" style={divStyle}><List>{this.renderMessages()}</List></div>
          <FormControl margin="normal" required fullWidth>
                    <InputLabel htmlFor="textsend">Message</InputLabel>
          <Input
            autoFocus={true}
            multiline={true}
            name="textsend"
            rowsMax={3}
            onChange={event => this.setState({ text: event.target.value })}
            value={this.state.text}
            onKeyPress={this.onSubmit}
          />
          </FormControl>
          <Button
            fullWidth
            variant="contained"
            color="primary"
            onClick={() => { this.setState({ btnsubmit: true });alert('S-a facut!!!');this.onSubmitbtn() }}
          >
          <Icon className={classNames('fa fa-paper-plane')} />&nbsp;&nbsp;Send
          </Button>
          <span ref={el => (this.bottomSpan = el)} />
        </div>
      )
    }
  }
  
  export default Chatcomp